$size-gap: 1px;
$size-square: 100px;
$size-container: (
  $size-square + $size-gap) * 7 - 1px;
$week: "mon" 0,
  "tue"$size-square + 1px,
  "wed"($size-square + $size-gap) * 2,
  "thu"($size-square + $size-gap) * 3,
  "fri"($size-square + $size-gap) * 4,
  "sat"($size-square + $size-gap) * 5,
  "sun"($size-square + $size-gap) * 6,;
